<template>
  <section class="information-container">
    <h2>
      Transform Your Social Media Profile. Join the
      <div class="animated" v-if="windowWidth < 720">{{ count }}+</div>
      <span v-else class="animated"> {{ count }}+ </span>
      Strong Community
    </h2>
  </section>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "CountedComponent",
  props: ["from", "to", "step"],
  data() {
    return {
      count: this.from,
    };
  },
  mounted() {
    let counterFunc = () => {
      this.count += this.step;
      if (this.count >= this.to) clearInterval(intervalCounter);
    };
    let intervalCounter = setInterval(counterFunc, 10);
  },
  computed: {
    ...mapGetters({
      windowWidth: "getWindowWidth",
    }),
  },
};
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 1921px) {
  .information-container {
    height: 20vh !important;
  }
}
@media only screen and (max-width: 720px) {
  .information-container {
    height: 50vh !important;
    padding: 2rem;
  }
}

.information-container {
  background: $theme-gradient-vertical-oppacity;
  display: grid;
  margin-top: 2rem;
  h2 {
    align-self: center;
    justify-self: center;
    color: $theme-color;
    font-family: sans-serif;
    font-weight: 700;
    margin-bottom: 22px;
    text-align: center;
    line-height: 1.7;
    font-size: 32px;
    background: linear-gradient(90deg, #af02ff 15%, #ff00b3e7 50%, #af02ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    .animated {
      color: #fff019;
      text-shadow: 3px 0px 17px rgba(70, 70, 0, 0.8),
        -3px 0px 7px rgba(255, 255, 0, 0.274), 0px 4px 7px rgb(70, 70, 0);
      font-size: 34px;
      -webkit-text-fill-color: #fff019;
      text-align: center;
    }
  }
}
.text-gra {
}
</style>
