<template>
  <div class="checkout-page">
    <v-row>
        <v-col cols="6">
            <v-text-field label="Card Number"></v-text-field>
        </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      pageReady: false,
      checkInterval: null,
    };
  },
  methods: {
    ...mapActions(["checkoutPage"]),
  },
  async created() {
    this.pageReady = await this.checkoutPage();
  },
  watch: {
    pageReady() {
      if (this.pageReady) {
        let fontAwesome = document.createElement("script");
        fontAwesome.setAttribute(
          "src",
          "https://www.google.com/recaptcha/api.js"
        );
        document.head.appendChild(fontAwesome);
      }
    },
  },

  computed: {
    ...mapGetters({
      checkoutIframe: "getCheckoutPage",
    }),
  },
};
</script>

<style lang="scss" scoped>
.checkout-page {
  min-height: 70vh;
}
</style>
