<template>
  <div class="carousel-container">
    <VueSlickCarousel
      v-bind="settings"
      class="carousel"
      :slidesToShow="slidesToShow"
      :slidesToScroll="slidesToScroll"
    >
      <CarouselCardItem v-for="(item, i) in items" :key="i" :item="item" />
    </VueSlickCarousel>
  </div>
</template>

<script>
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

import VueSlickCarousel from "vue-slick-carousel";
import CarouselCardItem from "./CarouselCardItem.vue";
import { mapGetters } from "vuex";
export default {
  components: { VueSlickCarousel, CarouselCardItem },
  props: {
    items: Array,
  },
  data() {
    return {
      settings: {
        dots: true,
        arrows: false,
        infinite: true,
        slidesToShow: 4,
        slidesToScroll: 3,
        speed: 500,
        autoplay: false,
        autoplaySpeed: 5000,
        cssEase: "linear",
        pauseOnDotsHover: true,
        pauseOnFocus: true,
        pauseOnHover: true,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true,
            },
          },
          {
            breakpoint: 720,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              initialSlide: 0,
            },
          },
        ],
      },
    };
  },

  computed: {
    ...mapGetters({
      windowWidth: "getWindowWidth",
    }),
    slidesToShow() {
      return this.windowWidth < 720 ? 1 : 5;
    },
    slidesToScroll() {
      return this.windowWidth < 720 ? 1 : 3;
    },
  },
};
</script>
<style lang="scss" scoped>
.carousel-container {
  display: grid !important;
  padding: 25px;
  .carousel {
    display: grid !important;

    div v-deep {
      // justify-self: center;
      display: grid !important;
    }
  }
}
.slick-list {
  padding: 0 2rem !important;
}
</style>
