<template>
  <v-col cols="12" class="cart-item">
    <v-row>
      <v-col cols="5" md="6">
        <v-icon>mdi-account</v-icon> {{ item.name }}
      </v-col>
      <v-col cols="2" md="3" style="font-size: 16px">
        <span class="old-price">
          {{ (item.price + (item.price / 100) * 33).toFixed(2) }}$
        </span>
        <span style="color: green">{{ item.price }}$ </span>
      </v-col>
      <v-col cols="3" md="2" style="font-size: 16px">
        <v-icon @click="removeCart(item)">mdi-minus</v-icon>
        {{ item.quantity }} <v-icon @click="addCart(item)">mdi-plus</v-icon>
      </v-col>
      <v-col cols="1" md="1">
        <v-icon @click="removeCart(item)" color="red"
          >mdi-trash-can-outline</v-icon
        >
      </v-col>
    </v-row>
  </v-col>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "CartItem",
  props: ["item"],
  methods: {
    ...mapMutations({
      removeCart: "removeCartItem",
      addCart: "addCartItem",
    }),
  },
};
</script>

<style lang="scss" scoped>
.cart-item {
  &:hover {
    background-color: rgb(201, 201, 201);
  }
}
.old-price {
  color: red;
  position: relative;
  opacity: 0.7;
  &::after {
    content: "";
    position: absolute;
    width: 110%;
    height: 2px;
    opacity: 0.8;
    background-color: #000;
    transform: translateY(-50%) rotate(-20deg);
    top: 50%;
    left: -5%;
  }
}
</style>
