const state = {
  windowWidth: window.innerWidth,
};
const getters = {
  getWindowWidth: (state: any) => {
    return state.windowWidth;
  },
};
const mutations = {
  resizeWindow: (state: any, payload: number): any => {
    state.windowWidth = payload;
  },
};
const actions = {};
export default { state, getters, mutations, actions };
