const state = {
  cart: [],
};
const getters = {
  getCartItems: (state: any) => {
    return state.cart;
  },

  getCartTotalPrice: (state: any) => {
    return state.cart.reduce((previous: number, current: any) => {
      return previous + current.price * current.quantity;
    }, 0).toFixed(2);
  },
  getCartUndiscountPrice: (state: any) => {
    return state.cart.reduce((previous: number, current: any) => {
      return (
        previous +
        (current.price + (current.price / 100) * 33) * current.quantity
      );
    }, 0).toFixed(2);
  },
};
const mutations = {
  addCartItem: (state: any, payload: any) => {
    if (!state.cart.find((item: any) => item?._id === payload?._id)) {
      state.cart.push({ ...payload, quantity: 1 });
    } else {
      state.cart = state.cart.map((item: any) => {
        if (item?._id === payload?._id)
          return { ...item, quantity: item.quantity + 1 };
        else return item;
      });
    }
  },
  removeCartItem: (state: any, payload: any) => {
    if (state.cart.find((item: any) => item._id === payload._id)) {
      if (payload.quantity > 1)
        state.cart = state.cart.map((item: any) => {
          if (item?._id === payload._id)
            return { ...item, quantity: item.quantity - 1 };
          else return item;
        });
      else if (payload.quantity <= 1) {
        state.cart = state.cart.filter((item: any) => item._id !== payload._id);
      }
    }
  },
};
const actions = {};

export default { state, getters, mutations, actions };
