<template>
  <v-app-bar class="appbar-theme px-4" height="100">
    <router-link to="/" class="icon-home">Social Parrot</router-link>
    <v-spacer></v-spacer>
    <v-row justify="end" v-if="windowWidth > 600">
      <v-col class="list-item btn" cols="2">
        <router-link to="buy-insta-follower" class="link-item">
          Instagram Follower
        </router-link>
      </v-col>
      <v-col class="list-item btn" cols="2">
        <router-link to="buy-insta-likes" class="link-item">
          Instagram Likes
        </router-link>
      </v-col>
      <template v-if="!authStatus">
        <v-col class="list-item btn" cols="2">
          <router-link to="/signin" class="link-item"> Sign In </router-link>
        </v-col>
        <v-col class="list-item" cols="2">
          <router-link class="btn box-btn" to="/signup"> Sign Up </router-link>
        </v-col>
      </template>
      <template v-else>
        <v-col class="list-item btn" cols="2">
          <router-link to="/profile" class="link-item"> Profile </router-link>
        </v-col>
        <v-col cols="2" class="list-item btn">
          <router-link to="/cart" class="link-item">
            Cart <v-icon color="#fff" class="ml-2"> mdi-cart-variant</v-icon>
            <v-badge v-if="cartItems.length" :content="cartItems.length"  color="red" >
              <v-icon icon="mdi-cart-variant" size="x-large"></v-icon>
            </v-badge>
          </router-link>
        </v-col>
        <v-col class="list-item btn" cols="2">
          <div @click="logout()" class="link-item">Log Out</div>
        </v-col>
      </template>
    </v-row>
    <v-icon v-else color="white" class="mobile-list-icon" @click="openSideBar"
      >{{ !sideBarShow ? "mdi-format-list-bulleted" : "" }}
    </v-icon>
    <template>
      <div class="sidebar-container" :class="[sideBarShow ? 'open' : 'close']">
        <div class="pa-8">
          <div>
            <v-icon @click="openSideBar" color="white" class="icon-task">
              mdi-window-close
            </v-icon>
          </div>
          <v-row justify="start" v-if="sideBarShow" class="pa-4">
            <v-col class="sidebar list-item btn my-4" cols="12">
              <router-link to="/">Home</router-link>
            </v-col>
            <v-col class="sidebar list-item btn my-4" cols="12">
              <router-link to="buy-insta-follower"
                >Instagram Followers</router-link
              >
            </v-col>
            <v-col class="sidebar list-item btn my-4" cols="12"
              ><router-link to="buy-insta-likes"
                >Instagram Likes</router-link
              ></v-col
            >
            <v-col class="sidebar list-item btn my-4" cols="12">Sign In</v-col>
            <v-col class="sidebar list-item btn box-btn my-4" cols="12">
              <router-link to="/signup"> Sign Up </router-link>
            </v-col>
          </v-row>
        </div>
      </div>
    </template>
  </v-app-bar>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "HeaderComponent",
  data() {
    return {
      sideBarShow: false,
    };
  },

  methods: {
    ...mapActions(["logout"]),
    openSideBar() {
      this.sideBarShow = !this.sideBarShow;
    },
  },
  computed: {
    ...mapGetters({
      windowWidth: "getWindowWidth",
      authStatus: "getAuthStatus",
      cartItems: "getCartItems",
    }),
  },
};
</script>

<style lang="scss">
@media only screen and (max-width: 1921px) {
  .appbar-theme {
    z-index: 9999;
  }
}

@media only screen and (max-width: 720px) {
  .appbar-theme {
    position: relative;
    z-index: 9999;
    margin-top: 18rem;
  }
}

.appbar-theme {
  background: $theme-gradient-color !important;
  color: $text-color !important;

  .list-item {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .list-item.btn {
    transition: all 250ms ease-in-out;
    cursor: pointer;
    font-size: 20px !important;
    font-weight: 500;
    .link-item {
      color: $text-color;
      text-decoration: none;
    }
    &:hover {
      scale: 1.1;
    }
    // .list-item.btn:not(:first-child) {
    //   border-left: 1px solid wheat;
    // }
  }
  .btn.box-btn {
    border: 1px solid $text-color;
    box-shadow: inset 0 0 10px #ffffff;
  }

  .mobile-list-icon {
    cursor: pointer;
    transition: 0.5s;
  }
}

.sidebar-container {
  position: absolute;
  right: 0;
  top: 0;
  background: $theme-gradient-vertical;
  transition: 0.5s;
  z-index: 9999;
  a {
    color: white !important;
    text-decoration: none;
  }

  &.open {
    width: 100vw;
    height: 100vh;
    .icon-task {
      transition: all 1s ease-in-out;
      rotate: 90deg;
      scale: 1.2;
    }
    .sidebar.list-item {
      transition: all 0.3s ease-in-out;
      scale: 1.1;
      &:hover {
        scale: 1.2;
      }
    }
  }
  &.close {
    width: 0px;
    height: 100vh;
    .icon-task {
      transition: all 0.1s ease-in-out;
      rotate: 180deg;
      scale: 1.5;
    }
  }
}
.icon-home {
  color: white !important;
  text-decoration: none;
}
a {
  color: white !important;
  text-decoration: none;
}
</style>
