<template>
  <v-row class="cart" justify="space-between">
    <v-col cols="12" md="8">
      <v-row class="head">
        <v-col cols="6" md="6">Service Name</v-col>
        <v-col cols="3" md="3">Price</v-col>
        <v-col cols="3" md="3">Quantity</v-col>
      </v-row>
      <v-row class="body" v-if="cartItems.length">
        <CartItem
          v-for="(item, index) in cartItems"
          :item="item"
          :key="index"
        />
      </v-row>
      <v-row justify="center" class="pa-8 cart-no-item" v-else>
        <div class="mr-2 mt-5">There is no item in your cart.</div>
      </v-row>
      <v-row>
        <v-col cols="8">
          <v-text-field
            color="purple"
            prepend-icon="mdi-instagram"
            label="Instagram Link"
            v-model="socialLink"
            @blur="setInstagramLink(socialLink)"
          >
          </v-text-field>
        </v-col>
        <v-col cols="4">
          <template v-if="socialLink.length">
            <v-chip color="green" text-color="white">Completed</v-chip>
          </template>
          <template v-else
            ><v-chip color="red" text-color="white">Add</v-chip></template
          >
        </v-col>
      </v-row>
    </v-col>
    <v-col md="3" cols="12" v-if="cartItems.length">
      <CheckoutContent />
    </v-col>
  </v-row>
</template>

<script>
import CartItem from "@/components/shared/CartItem.vue";
import CheckoutContent from "./CheckoutContent.vue";
import { mapGetters, mapMutations } from "vuex";
export default {
  data() {
    return {
      socialLink: "",
    };
  },
  components: {
    CartItem,
    CheckoutContent,
  },
  methods: {
    ...mapMutations(["setInstagramLink"]),
  },
  mounted() {
    if (this.instagramLink.length) this.socialLink = this.instagramLink;
  },

  computed: {
    ...mapGetters({
      cartItems: "getCartItems",
      instagramLink: "insLink",
    }),
  },
};
</script>

<style lang="scss" scoped>
.cart {
  padding: 1.25rem;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 2rem;
  min-height: 70vh;
}
.head {
  background: linear-gradient(
    90deg,
    rgba(4, 92, 153, 1) 0%,
    rgba(10, 147, 218, 1) 50%,
    rgba(1, 103, 210, 1) 100%
  );
  color: #fff;
  font-weight: 400;
  font-size: 18px;
}
.body {
  background: rgba($color: #fff, $alpha: 1);
  font-size: 18px;
}

.cart-no-item {
  color: rgba($color: #9b9b9b, $alpha: 0.8);
  font-size: 26px;
}
</style>
