import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import MainPage from "@/views/MainPage.vue";
import LayoutPage from "@/views/LayoutPage.vue";
import InstaFollower from "@/views/InstaFollower/InstaFollower.vue";
import InstaLikes from "@/views/InstaLikes/InstaLikes.vue";
import HomeComponent from "@/views/MainPage/HomePage.vue";
import SignupPage from "@/views/Authentication/SignupPage.vue";
import SigninPage from "@/views/Authentication/SigninPage.vue";
import ShoppingCart from "@/views/Cart/ShoppingCart.vue";
import CheckoutPage from "@/views/Checkout/CheckoutPage.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "home",
    component: HomeComponent,
  },
  {
    path: "/signup",
    name: "signup",
    component: SignupPage,
  },
  {
    path: "/signin",
    name: "signin",
    component: SigninPage,
  },
  {
    path: "/cart",
    name: "cart",
    component: ShoppingCart,
    meta: { title: "Shopping Cart" },
  },
  {
    path: "/checkout",
    name: "checkout",
    component: CheckoutPage,
    meta: { title: "Checkout Page" },
  },
  {
    path: "/",
    name: "layout",
    component: LayoutPage,
    children: [
      {
        path: "/buy-insta-follower",
        name: "insta-follower",
        component: InstaFollower,
        meta: { title: "Buy Instagram Follower" },
      },
      {
        path: "/buy-insta-likes",
        name: "insta-likes",
        component: InstaLikes,
        meta: { title: "Buy Instagram Likes" },
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
