import { signup, login, logout, checkAuth } from "@/utils/ApiService";
import axios from "axios";
import router from "@/router";

const state = {
  auth: false,
};
const getters = {
  getAuthStatus: (state: any) => {
    return state.auth;
  },
};
const mutations = {};
const actions = {
  async signup({ state }: { state: any }, payload: any) {
    const result = await signup(payload);
  },

  async login({ state }: { state: any }, payload: any) {
    const response = await login(payload);
    if (response.data.status === "success") {
      sessionStorage.setItem("logedIn", response.data.token);
      axios.defaults.headers.common[
        "Authorization"
      ] = `Bearer ${response.data.token}`;
      state.auth = true;
      router.push("/buy-insta-follower");
      return true;
    } else if (response.data.status === "error") {
      state.auth = false;
      return false;
    }
  },

  async logout({ state }: { state: any }) {
    const result = await logout();
    if (result.data.status === "success") {
      delete axios.defaults.headers.common["Authorization"];
      state.auth = false;
      sessionStorage.removeItem("logedIn");
    } else {
      console.log("error");
    }
  },

  async checkAuth({ state }: { state: any }) {
    const userToken = sessionStorage.getItem("logedIn");
    const response = await checkAuth({ token: userToken || "" });
    if (response.data.status === "success") {
      state.auth = true;
      axios.defaults.headers.common[
        "Authorization"
      ] = `Bearer ${response.data.token}`;
    } else {
      delete axios.defaults.headers.common["Authorization"];
      state.auth = false;
    }
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
