import { getProducts } from "../../utils/ApiService";

const state = {
  followerCardData: [],
};
const getters = {
  getFollowersCard: (state: any) => {
    return state.followerCardData.sort((a: any, b: any) =>
      a.amount > b.amount ? 1 : -1
    );
  },
};
const mutations = {
  setFollowerCards: (state: any, payload: any) => {
    state.followerCardData = payload;
  },
};
const actions = {
  fetchFollowers: ({ commit }: { commit: any }) => {
    getProducts()
      .then((res) => {
        commit("setFollowerCards", res.data.data);
      })
      .catch((err) => console.log(err));
  },
};
export default { state, getters, mutations, actions };
