<template>
  <v-row>
    <v-col cols="12" class="cart-header">Shop Cart</v-col>
    <v-col cols="12">
      <v-row class="section">
        <v-col>Total Price :</v-col>
        <v-col>{{ undiscountPrice }} $</v-col>
      </v-row>
    </v-col>
    <v-col cols="12" class="section">
      <v-row>
        <v-col> Total Discount :</v-col>
        <v-col style="color: green"
          >{{ (undiscountPrice - totalPrice).toFixed(2) }} $</v-col
        >
      </v-row>
    </v-col>
    <v-col cols="12">
      <v-row>
        <v-col> Payment Price : </v-col>
        <v-col style="font-weight: 600;">{{ totalPrice }} $</v-col>
      </v-row>
    </v-col>
    <v-col cols="12">
      <button v-if="cartItems.length" class="checkout-btn" @click="routeToCheckout()">Check Out</button>
    </v-col>
  </v-row>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  methods: {
    routeToCheckout() {
      if (this.cartItems.length) this.$router.push("/checkout");
    },
  },
  computed: {
    ...mapGetters({
      totalPrice: "getCartTotalPrice",
      undiscountPrice: "getCartUndiscountPrice",
      cartItems: "getCartItems",
    }),
  },
};
</script>

<style lang="scss" scoped>
.cart-header {
  background: linear-gradient(
    90deg,
    rgba(4, 92, 153, 1) 0%,
    rgba(10, 147, 218, 1) 50%,
    rgba(1, 103, 210, 1) 100%
  );
  color: #fff;
  font-weight: 500;
}

.checkout-btn {
  background-image: linear-gradient(
    to right,
    #0d0177 0%,
    #11b8e2 51%,
    #21224d 100%
  );
  color: #fff;
  padding: 0.875rem;
  font-size: 16px;
  font-weight: 600;
  width: 100%;

  &:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none;
  }
}
.section {
  border-bottom: 1px solid rgb(44, 44, 44);
}
</style>
