import { getCheckoutPage } from "../../utils/ApiService";

const state = {
  instagramLink: "",
  checkoutPage: {},
};
const getters = {
  insLink: (state: any) => {
    return state.instagramLink;
  },
  getCheckoutPage: (state: any) => {
    return state.checkoutPage;
  },
};
const mutations = {
  setInstagramLink: (state: any, payload: string) => {
    state.instagramLink = payload;
  },
  setCheckoutPage: (state: any, payload: any) => {
    state.checkoutPage = payload;
  },
};
const actions = {
  checkoutPage: ({ commit }: { commit: any }) => {
    return new Promise((resolve, reject) => {
      getCheckoutPage().then((res) => {
        resolve(res.data);
      });
    });
  },
};
export default { state, getters, mutations, actions };
