<template>
  <div class="badge-container" :style="windowWith < 720 ? '100%' : '75%'">
    <span class="badge-text"> {{ pageTitle }}</span>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "BadgeComponent",
  data() {
    return {
      pageTitle: "",
    };
  },
  watch: {
    "$route.meta.title"(newVal, oldVal) {
      this.pageTitle = newVal;
    },
  },
  mounted() {
    this.pageTitle = this.$route.meta.title;
  },
  computed: {
    ...mapGetters({
      windowWith: "getWindowWidth",
    }),
  },
};
</script>

<style lang="scss" scoped>
.badge-container {
  padding: 14px 25px;
  text-align: center;
  border-radius: 25px 25px 15px 15px;
  margin: 1.25em 0 0 1.25em;
  background: linear-gradient(
    90deg,
    rgba(97, 37, 226, 0.616) 0%,
    rgba(135, 82, 240, 0.6867121848739496) 28%,
    rgba(207, 95, 255, 0.6278886554621849) 52%,
    rgba(174, 2, 248, 0.547) 74%,
    rgba(215, 211, 216, 0.5) 100%
  );
  box-shadow: rgba(250, 250, 250, 0.04) 11px 1px 25px,
    rgba(255, 255, 255, 0.04) 111px 1px 10px 25px;
  .badge-text {
    font-weight: 600;
    font-size: 24px;
    color: white !important;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
    text-shadow: 3px 0px 17px rgb(255, 255, 255), -3px 0px 7px rgb(117, 20, 101),
      0px 4px 7px rgb(183, 0, 255);
  }
}
</style>
