<template>
  <section class="py-8">
    <h2>Sign In</h2>
    <p>Access your order and more features please sign in.</p>
    <v-row justify="center">
      <v-col cols="12" md="4" class="pa-8">
        <div class="signup pa-12">
          <v-text-field
            label="Email"
            color="purple"
            outlined
            prepend-icon="mdi-email-outline"
            v-model="submit.email"
            :rules="[rules.email]"
          >
          </v-text-field>
          <v-text-field
            label="Passowrd"
            color="purple"
            outlined
            prepend-icon=" mdi-key"
            v-model="submit.password"
            :rules="[rules.password]"
          >
          </v-text-field>
          <p>
            Don't you have account
            <router-link to="signup"> Sign Up</router-link>
          </p>
          <v-btn @click="submitLogin()" color="purple"> Sign In </v-btn>
        </div>
      </v-col>
    </v-row>
  </section>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      submit: {
        email: "",
        password: "",
      },
      rules: {
        email: (value) => {
          if (!value || !value.trim()) {
            return "Email is required";
          }
          if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(value)) {
            return "Email must be valid";
          }
          return true;
        },
        password: (value) => {
          if (!value || !value.trim()) {
            return "Password is required";
          }
          if (value.length < 8) {
            return "Password must be at least 8 characters long";
          }
          if (!/(?=.*[a-z])(?=.*[A-Z])(?=.*\d).*/.test(value)) {
            return "Password must include at least one uppercase letter, one lowercase letter, and one number";
          }
          return true;
        },
      },
      logedIn: false,
    };
  },
  methods: {
    ...mapActions(["login"]),
    async submitLogin() {
      if (
        this.rules.email(this.submit.email) === true &&
        this.rules.password(this.submit.password) === true
      )
        this.logedIn = await this.login(this.submit);
    },
  },
};
</script>

<style lang="scss" scoped>
@media only screen and (max-width: 1921px) {
  section {
    height: calc(75vh);
  }
}
@media only screen and (max-width: 720px) {
  section {
    margin-top: 6.25rem;
    height: calc(100vh);
    padding-bottom: 8rem;
  }
}
section {
  background: $theme-gradient-vertical-oppacity;

  h2 {
    text-align: center;
    color: $theme-color;
  }
  p {
    font-size: 18px;
    font-weight: 500;
    text-align: center;
  }
  .signup {
    background: white;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
      rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
      rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    border-radius: 25px;
    display: grid;
    p {
      text-align: center;
    }
    a {
      color: $theme-color !important;
    }
    button {
      justify-self: center;
      color: white;
    }
  }
}
</style>
